import { Component } from '@angular/core';
import { ViewController } from 'ionic-angular';

@Component({
  selector: 'page-conditions',
  templateUrl: 'conditions.html'
})
export class ConditionPage {
  htmlConditions:any;

  constructor(public viewCtrl: ViewController) {

    this.htmlConditions = "<div><strong>CONDITIONS GENERALES D'UTILISATION </strong> <div id='definition' class='titre'>Article 1 : Définitions </div> Utilisateur : personne physique ou morale titulaire d'un compte d'accès au site privé de RADEEMA<br> <a href='http://www.RADEEMA.ma' class='lien'>www.RADEEMA.ma</a> : Nom du service fourni par RADEEMA qui a pour objet de fournir des informations sur l'activité clientèle de RADEEMA et de permettre aux personnes physiques titulaires d'un compte d'y accéder.<br> <br> Identifiant : Terme qui désigne d'une manière générale, tout code confidentiel ou mot de passe, permettant à l'utilisateur de s'identifier et de se connecter au service.<br> <br> Service : L'ensemble des services fournis à l'utilisateur par RADEEMA tels que Consulter le détail de son contrat, Avoir l'historique de ses consommations, Avoir la liste de ses impayés.<br> <br> Les utilisateurs peuvent également y inscrire tous les contrats d'eau et d'électricité qui sont en<br> leurs noms ou dont ils sont mandataires. <div id='objet' class='titre'>Article 2 : Objet </div> Les présentes conditions générales d'utilisation ont pour objet d'une part, de définir les modalités et les conditions de mise à disposition par RADEEMA de son service électronique, et d'autre part de permettre à l'utilisateur d'accéder et d'utiliser ce service.<br> <br> L'utilisateur reconnaît avoir pris connaissance des présentes conditions générales. <div id='acces' class='titre'>Article 3 : Conditions d'accès et d'utilisation </div> L'utilisation du Service est conditionnée par l'acceptation par l'utilisateur des présentes Conditions Générales d'Utilisation qu'il s'engage à respecter.<br> <br> Les Conditions Générales d'Utilisation applicables sont celles disponibles en ligne à la date de souscription du Service.<br> <br> <strong>3.1 Accès au service</strong><br> L'utilisateur reconnaît avoir été informé que le site est fourni sur la base d'un service « en l'état de l'art » et accessible en fonction de sa disponibilité.<br> RADEEMA ne pourra pas être tenue pour responsable d'une défaillance des fournisseurs d'accès à Internet (difficultés informatiques ou autres difficultés techniques...).<br> <br> Les modalités et opérations de tests de contrôle et de maintenance, ainsi que les interventions nécessaires en cas de panne, peuvent être effectuées par RADEEMA à tout moment.<br> <br> Il est rappelé que le secret des correspondances n'est pas garanti sur le réseau et qu'il appartient à chaque utilisateur d'Internet de prendre toutes les mesures appropriées de façon à protéger ses propres données et/ou logiciels de la contamination d'éventuels virus circulant sur Internet.<br> <br> <strong>3.2 code d'accès</strong><br> Les codes d'accès sont l'ensemble des éléments permettant à l'utilisateur de s'identifier et de se connecter au service. Ces codes sont personnels et confidentiels.<br> <br> L'utilisateur s'engage à ne pas divulguer son identifiant et son code d'accès aux tiers.<br> L'utilisateur est entièrement responsable de ses éléments d'identification ainsi que de l'usage qui en est fait. <div id='engagement' class='titre'>Article 4 : Engagement du client : </div> <strong>Le client s'engage à:</strong><br> <br> <ul> <li> déclarer accepter les caractéristiques et les limites du réseau Internet, et notamment, reconnaît qu'il a une parfaite connaissance de la nature du réseau Internet, et en particulier de ses performances techniques et des temps de réponse pour consulter, interroger ou transférer des informations.</li> <li> Garantir RADEEMA qu'il est l'utilisateur de la CB et qu'il dispose des autorisations nécessaires auprès de sa banque pour utiliser la CB dans le cadre du Service, notamment pour le paiement en ligne sur Internet.</li></ul> <div id='fraude' class='titre'>Article 5 : Usage frauduleux </div> RADEEMA se réserve le droit de suspendre l'accès à toute personne si elle devait relever des faits laissant présumer la tentative d'utilisation frauduleuse de ses services. L'intéressé en sera aussitôt informé, le cas échéant. <div id='tarification' class='titre'>Article 6 : Tarification </div> L'accès au site Internet RADEEMA est gratuit. Le client fait son affaire personnelle du règlement des communications dont le coût est directement facturé par l'exploitant du réseau de télécommunications. <div id='responsabilite' class='titre'>Article 7 : Responsabilités</div> RADEEMA n'est pas responsable du transport des données, de la qualité et de la disponibilité des réseaux de télécommunication, d'interruptions de service consécutives à un mauvais fonctionnement du matériel du client. <br> <br> RADEEMA n'est davantage pas responsable des conséquences de l'utilisation frauduleuse, par un tiers, du code utilisateur et du code confidentiel de l'utilisateur. Il appartient, d'autre part, au client de prendre toutes les précautions nécessaires pour protéger ses données personnelles stockées dans son ordinateur ou son système informatique (notamment contre les virus informatiques ou les piratages) <div id='protection' class='titre'>Article 8 : Protection des données à caractère personnel </div> Les informations saisies par l'utilisateur seront enregistrées par RADEEMA, responsable du traitement pour les seuls besoins de la fourniture du service les (Finalités) Les données personnelles de l'utilisateur seront analysées et transmises aux différents services concernés par ces Finalités. <br> <br> Les informations conservées par RADEEMA ne sont transmises qu'aux seules personnes physiques et morales qui sont habilitées à les connaître. Néanmoins, RADEEMA pourra les communiquer à ses sous-traitants pour des besoins de gestion et/ou de recouvrement, quel qu'en soit le support. RADEEMA peut être amenée à communiquer ces données lorsque cette démarche est imposée par la loi ou les autorités judiciaires. <br> <br> Ces informations pourront être communiquées aux prestataires de RADEEMA pour l'exécution de travaux effectués pour son compte dans le respect des Finalités.<br> <br> L'utilisateur est informé que la mise en oeuvre du Service utilise une application hébergée auprès d'un tiers prestataire de service. <br> <br> RADEEMA et ses prestataires de service externes utiliseront les mesures appropriées de sécurité physiques, techniques et organisationnelles afin de protéger vos données personnelles contre toute divulgation ou tout accès non autorisé. <br> <br> RADEEMA, dans le cadre de la fourniture du Service, conservera vos données personnelles pour la durée nécessaire aux Finalités. <br> <br> Conformément à la loi N°09-08 sur la protection des personnes physiques à l'égard du traitement des données à caractère personnel, le Client dispose à tout moment d'un droit individuel d'opposition, d'accès, de modification, de rectification et de suppression des données le concernant auprès de RADEEMA en écrivant au Centre Relation Client (CRC). <div id='intellectuelle' class='titre'>Article 9 : Propriété intellectuelle </div> L'utilisation, la reproduction ou représentation, intégrale ou partielle des pages, données, des logiciels et de tout autre élément constitutif du site, par quelque procédé ou support, existant ou futur, que ce soit, est interdite et constitue, sans notre autorisation et/ou celle de nos prestataires intervenant dans la fourniture du service, une contrefaçon, passible des sanctions prévues à <strong>la loi n° 17-97 relative à la protection de la propriété industrielle</strong> <div id='confidentialite' class='titre'>Article 10 : Confidentialité </div> Les données à caractère personnel relatives aux clients sont considérées comme confidentielles conformément aux lois et réglementations en vigueur, L'utilisateur dispose seul d'un droit individuel d'accès et de rectification des informations le concernant, <div id='preuves' class='titre'>Article 11 : Preuves </div> Les données qui sont communiquées dans le cadre des présentes conditions générales le sont à titre indicatif Seuls les relevés des historiques de consommation délivrés en agences RADEEMA, feront foi. <div id='integralite' class='titre'>Article 12 : Intégralité des engagements </div> Les présentes conditions générales d'utilisation constituent la totalité de l'accord passé entre RADEEMA et l'utilisateur pour la souscription et l'utilisation du service. <div id='droit' class='titre'>Article 13 : Droit applicable et règlement de litige </div> Tout litige portant sur l'interprétation ou l'exécution des présentes sera soumis à la compétence exclusive du Tribunal de Commerce de MARRAKECH, la loi marocaine étant applicable au fond et à la procédure.</div>"
  }

  closeModal() {
    this.viewCtrl.dismiss();
  }
}


